<template>
  <div class="pem-download-bar" v-show="showFlag">
    <div class="pem-download-bar-wrapper">
      <div class="pem-download-bar-logo">
        <pem-icon name="bottom" width="64" height="64" />
      </div>
      <div class="pem-download-bar-text">
        <div class="pem-download-bar-title">{{ title }}</div>
        <div v-if="desc" class="pem-download-bar-desc">{{ desc }}</div>
      </div>
      <router-link tag="div" class="pem-download-bar-btn" to="/download">
        立即下载
      </router-link>
      <div class="pem-download-bar-close-btn" @click="closeDownloadBar">
        <pem-icon name="input-Eliminate" width="28" height="28" />
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  name: 'pem-download-bar',
  props: {
    title: {
      type: String,
      default: '项目工场'
    },
    desc: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showFlag: true
    }
  },
  components: {
    PemIcon
  },
  methods: {
    hide () {
      this.showFlag = false
    },
    show () {
      this.showFlag = true
    },
    closeDownloadBar () {
      this.hide()
    }
  }
}
</script>



