<template>
  <div class="investor-all-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="list"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <div class="investor-all-list-title">全部投资人</div>
          <pem-investor-list :data="list"  @alert="alert"/>
          <div class="scroll-view-pullup">{{isMore ? '加载中...' : '暂时就这么多了...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" bottom="344"/>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemInvestorList from 'components/pem-investor-list/pem-investor-list'
import PemDownloadBar from 'components/download-bar/download-bar'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { INVESTOR_ALL_LIST_CLEAR, INVESTOR_ALL_LIST_UPDATE } from 'store/minisite/investor-all-list'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemInvestorList,
    PemDownloadBar,
    PemScrollView,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      list: [],
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    investorAllList: state => state.investorAllList
  }),
  created () {
    this._initWXShare()
    this._setTitle('项目工场-投资人')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  beforeDestroy () {
    let _investorAllList = this.investorAllList
    _investorAllList.y = this.$refs.scroll.scroll.y
    this.INVESTOR_ALL_LIST_UPDATE(_investorAllList)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getInvestorList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getInvestorList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getInvestorList('init')
      }
    },
    getInvestorList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.investorAllList &&
        this.investorAllList.list &&
        this.investorAllList.list.length > 0 &&
        this.currentPage <= this.investorAllList.currentPage) {
        this.isLoading = false
        this.currentPage = this.investorAllList.currentPage + 1
        if (+this.investorAllList.totalPage <= +this.investorAllList.currentPage) {
          this.isMore = false
        }
        this.list = this.investorAllList.list || []
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.investorAllList.y)
        }, 21)
        return false
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }
      this._getInvestorList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          _list = _list.concat(data.resultList || []).map(item => {
            if (item.logo && item.logo.indexOf('user_xcx/default_investor_img') === -1) {
              item['logo'] = ImgPath + item.logo.replace(ImgPath, '')
            } else {
              let userZj = (item.zj || '1').toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
          this.INVESTOR_ALL_LIST_CLEAR()
          this.INVESTOR_ALL_LIST_UPDATE({
            list: _list,
            currentPage: +data.currentPage || 1,
            totalPage: +data.totalPage || 1,
            y: 0
          })
          this.list = _list
        }
      })
    },
    _getInvestorList (options, callback) {
      MiniSiteApi.getInvestorList(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      INVESTOR_ALL_LIST_UPDATE,
      INVESTOR_ALL_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
  .investor-all-list-title
    padding: 0.4rem 0.3rem 0
    font-size: 0.36rem
    font-weight: bold
    line-height: 0.5rem
    color: $color-text
</style>



